export const makeSchedularEvent = (id, sendInfos) => {
  return axios
    .post(`${make_another_schedular}/${id}`, sendInfos)
    .then((response) => {
      return response;
    })
    .catch((err) => {
      return { error: err.response.data };
    });
};
export const updateSchedularEvent = (sub, sendInfos, id) => {
  return axios
    .put(`${make_another_schedular}/?user=${sub}&id=${id}`, sendInfos)
    .then((response) => {
      return response;
    })
    .catch((err) => {
      return { error: err.response.data };
    });
};

export const deleteSchedularEvent = (sub, id) => {
  return axios
    .delete(`${make_another_schedular}/?user=${sub}&id=${id}`)
    .then((response) => {
      return response;
    })
    .catch((err) => {
      return { error: err.response.data };
    });
};

export const getUsers = () => {
  return axios
    .get(`${getuser_url}`)
    .then((response) => {
      return response;
    })
    .catch((err) => {
      return { error: err.response.data };
    });
};
export const apiGetEmailVerificationCode = (email) => {
  return axios
    .post(`${getEmailVerificationCode_URL}`, email)
    .then((response) => {
      return response;
    })
    .catch((err) => {
      return { error: err.response.data };
    });
};
export const apiCheckEmailVerificationCode = (code) => {
  const token = localStorage.getItem('dento');
  const config = {
    headers: { 'x-access-token': token ? token : '' }
  };
  return axios
    .post(`${checkEmailVerificationCode_URL}`, code, config)
    .then((res) => {
      return res;
    })
    .catch((err) => {
      return { error: err.respose.data };
    });
};
export const apiResetPassword = (pass) => {
  const token = localStorage.getItem('dento');
  const config = {
    headers: { 'x-access-token': token ? token : '' }
  };
  return axios
    .post(`${resetPassword_URL}`, { password: pass }, config)
    .then((res) => {
      return res;
    })
    .catch((err) => {
      return { error: err.respose.data };
    });
};

export const getIntentList = () => {
  return axios
    .get(`${get_list_intent}`)
    .then((response) => {
      return response;
    })
    .catch((err) => {
      return { error: err.response.data };
    });
};
